import { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

const Create = () => {
    const [title, setTitle] = useState('');
    const [body, setBody] = useState('');
    const [excerpt, setExcerpt] = useState('');
    const [tags, setTags] = useState('');

    const [author, setAuthor] = useState('');
    const [isPending, setIsPending] = useState(false);
    const history = useHistory();
    const [players, stplayers] = useState([]);

    
    const changeBoy = (e) => {
        e.preventDefault();
        setAuthor(e.target.value)
        console.log(author)

    }

    useEffect(()=>{
        fetch("https://ggg-blog-back.herokuapp.com/auths").then(
            res => res.json()
        ).then(
            data=> {
                stplayers(JSON.parse(data))
                // console.log(players)
            }
        )
    },[])

    const handleSubmit = (e) => {
        e.preventDefault();
        const blog = { title, body, author, excerpt, tags };

        setIsPending(true);

        fetch('https://ggg-blog-back.herokuapp.com/blogs', {
            method: 'POST',
            headers: {"Content-Type": "application/json"},
            body: JSON.stringify(blog)
        }).then(() => {
            setIsPending(false);
            history.push('/');
        })
    }

    return ( 
        <div className="create">
            <h2>Add a New Blog</h2>
            <form onSubmit={handleSubmit}>
                <label>Blog Title</label>
                <input 
                    type="text"
                    required
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                />
                <label>Blog Abstract</label>
                <input 
                    type="text"
                    required
                    value={excerpt}
                    onChange={(e) => setExcerpt(e.target.value)}
                />
                <label>Blog Body:</label>
                <textarea
                    required
                    value={body}
                    onChange={(e) => setBody(e.target.value)}
                />
                <label>Blog Tags</label>
                <input 
                    type="text"
                    required
                    value={tags}
                    onChange={(e) => setTags(e.target.value)}
                />
                <label>Blog author:</label>
                <select onChange={changeBoy} defaultValue="Choose Author">
            {players.map((players, index) => {
                return (
                <option key={players.id} value={players.id}>{players.value}</option>
                )
            
                }
            )}
            <option value="Choose Author" selected disabled hidden>Choose Author</option>
            </select>
                {!isPending && <button>Add Blog</button>}
                {isPending && <button disabled>Adding Blog</button>}
            </form>
        </div>
     );
}

export default Create;