import { Link } from 'react-router-dom';
import Logo from "./ggg-logo.png";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const Navbar = () => {
    return (
        <>
        <Row>

            <Col>
            <Link to="/">
            <img src={Logo} alt="logo" className="photo" align= "left"/>
            </Link>
            
            </Col>
            <Col>
            
            </Col>
            <Col></Col>


        </Row>
        <Row>
        <nav className="navbar">
            <div className="links">
    {/* <div id="logo">Logo</div> */}
                <Link to="/" align="right">

                    Home</Link>
                <Link to="/make" align="right">Create Blog</Link>
                <Link to="/update" align="right" >Manage Profile</Link>
            </div>
        </nav>
        </Row>
               
     
        </>
    )
}

export default Navbar;