import { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';



const UserCreate = () => {
    const [userName, setUserName] = useState('');
    const [descr, setDescr] = useState('');
    const [prof, setProf] = useState('');
    const [img, setImg] = useState();
    const [isPending, setIsPending] = useState(false);
    const history = useHistory();
    const [players, stplayers] = useState([]);
    const [worker, stWorker] = useState([]); 
    const [userid,stUserid]= useState([]);
    const [isPending2, setIsPending2] = useState(true);

    var state = {
        selectedFile: null
    }
    const fileselected = event => {
        console.log(event.target.files[0])
        state.selectedFile = event.target.files[0]
        console.log('selected')
        // setImg(URL.createObjectURL(state.selectedFile))
        // console.log(img)
    }

    const changeBoy = (e) => {
        e.preventDefault();
        stUserid(e.target.value)
        fetch("https://ggg-blog-back.herokuapp.com/users/" + e.target.value).then(
            res => res.json(),

        ).then(
            daata=> {
            
            stWorker(daata)
            setUserName(worker.AuthorName)
            setDescr(worker.Descr)
            setProf(worker.Position)
            console.log(daata)

            },
            setIsPending2(false)

        )

    }
    const handleSubmit = (e) => {
        e.preventDefault();
        const fd = new FormData();
        fd.append('img', state.selectedFile, state.selectedFile.name);

        fd.append('uname', userName);
        fd.append('desscr',descr);
        fd.append('prof',prof);
        const blog = { userName, descr, prof};
        setIsPending(true);
        const url = 'https://ggg-blog-back.herokuapp.com/upd/'+userid
        // console.log(userid)
        fetch(url, {
            method: 'POST',
            // headers: {"Content-Type": "multipart/form-data"},
            body: fd
        }).then(() => {
            setIsPending(false);
            history.push('/');
        })
    }

    useEffect(()=>{
        fetch("https://ggg-blog-back.herokuapp.com/auths").then(
            res => res.json()
        ).then(
            data=> {
                stplayers(JSON.parse(data))
                // console.log(players)
            }
        )
    },[])

    return ( 
        <div className="create">
            <h2>Manage your Profile</h2>
            <select onChange={changeBoy} defaultValue="Choose Employee" >
            {players.map((players, index) => {
                return (
                <option key={players.id} value={players.id}>{players.value}</option>
                )
            
                }
            )}
            <option value="Choose Employee" disabled hidden> Choose Employee </option>
            </select>
            


            <form onSubmit={handleSubmit}>
                <label>User Name</label>
                <input 
                    type="text"
                    disabled={isPending2}
                    required
                    defaultValue={worker.AuthorName}
                    onChange={(e) => setUserName(e.target.value)}
                />
                <label>Short Description</label>
                <textarea
                    required
                    disabled={isPending2}
                    defaultValue={worker.Descr}
                    onChange={(e) => setDescr(e.target.value)}
                />
                <label>Profession</label>
                <input 
                    type="text"
                    disabled={isPending2}
                    required
                    defaultValue={worker.Position}
                    onChange={(e) => setProf(e.target.value)
                    }
                />
            
                <label> Profile Image</label>
                <input type="file"
                    disabled={isPending2}
                    id="img" name="img" 
            accept="image/*" className="file-custom"
            value={img}
            // onChange={(e) => setImg(e.target.value)}
            onChange = {fileselected}
            />
            
                {!isPending && <button>Update Information</button>}
                {isPending && <button disabled>Updating Info..</button>}
            </form>
        </div>
     );
}

export default UserCreate;