import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Home from './components/pages/Home'
import BlogDetails from './components/pages/BlogPage'
import NotFound from './components/NotFound'
import Create from './components/pages/BlogMake'
import UserCreate from './components/pages/UserMake'
import Navbar from'./components/pages/NavBar';
import Footer from'./components/Footer';

import 'bootstrap/dist/css/bootstrap.min.css';


function App() {

  return (
    <Router>
      <Navbar/>
      <Switch>
        <div className='fronts'>
        <Route path='/' exact component= {Home}/>
        <Route path='/make' exact component= {Create}/>
        <Route path='/update' exact component= {UserCreate}/>

        <Route path="/blogs/:id">
              <BlogDetails/>
            </Route>
            {/* <Route path = "" component= {NotFound}/> */}
        </div>
      </Switch>
      <Footer/>

    </Router>
  );
}

export default App;
